                        <div class="alert alert-success" role="alert">
                            {{ session('status') }}
                        </div>
                    @endif

                    <form id="search">
                     @csrf

                     {{ ('Search by name') }}
                     <input class="check__input" name="name" type="text" value="">

                     {{ ('Search by bedrooms') }}
                     
                     <input class="check__input" name="bedrooms" type="text" value="">

                     {{ ('Search by bathrooms') }}
                     
                     <input class="check__input" name="bathrooms" type="text" value="">

                     {{ ('Search by storeys') }}
                     
