</template>

<script>
    export default {
        mounted() {
            console.log('Component mounted.')
        },
        data() {
            return {
              name: '',
              description: '',
              output: ''
            };
        },
        methods: {
            submitForm: function(e) {
                e.preventDefault();
                let currentObj = this;
                this.axios.post('http://site/api/getFlatByParam', {
                    "_token": "{{ csrf_token() }}",
                    name: this.name,
                    description: this.description
                })
                .then(function (response) {
                    currentObj.output = response.data;
                })
                .catch(function (error) {
                    currentObj.output = error;
                });
            }
        }
    }
</script>